
/**
è®¾ç½®å±žæ€§éœ€è¦è€ƒè™‘ä¸€äº›ç‰¹æ®Šæƒ…å†µï¼Œæˆ‘ä»¬å•ç‹¬å°†å…¶æ‹¿å‡ºæ¥ä½œä¸ºä¸€ä¸ªæ–¹æ³•setAttribute
 */
function setAttribute(dom, name, value) {
  // å¦‚æžœå±žæ€§åæ˜¯classname,åˆ™æ”¹å›žclass
  if (name === 'className') name = 'class';

  // å¦‚æžœå±žæ€§åæ˜¯onXXX,åˆ™æ˜¯ä¸€ä¸ªäº‹ä»¶ç›‘å¬æ–¹æ³•
  if (/on\w+/.test(name)) {
    name = name.toLowerCase();
    dom[name] = value || ''
    // å¦‚æžœå±žæ€§åæ˜¯style,åˆ™æ›´æ–°styleå¯¹è±¡
  } else if (name === 'style') {
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || ''
    } else if (value && typeof value === 'object') {
      for(let name in value) {
        dom.style[name] = typeof value[name] === 'number' ? value[name] + 'px' : value[name];
      }
    }
  } else {
    if (name !== "class" && name in dom) {
      dom[name] = value || ''
    }
    if (value) {
      dom.setAttribute(name, value)
    } else {
      dom.removeAttribute(name)
    }
  }
  // æ™®é€šå±žæ€§åˆ™ç›´æŽ¥æ›´æ–°å±žæ€§
}
/**
  ä»Žjsxè½¬è¯‘ç»“æžœæ¥çœ‹ï¼ŒcreateElementæ–¹æ³•çš„å‚æ•°æ˜¯è¿™æ ·ï¼š

  createElement( tag, attrs, child1, child2, child3 );
  ç¬¬ä¸€ä¸ªå‚æ•°æ˜¯DOMèŠ‚ç‚¹çš„æ ‡ç­¾åï¼Œå®ƒçš„å€¼å¯èƒ½æ˜¯divï¼Œh1ï¼Œspanç­‰ç­‰
  ç¬¬äºŒä¸ªå‚æ•°æ˜¯ä¸€ä¸ªå¯¹è±¡ï¼Œé‡Œé¢åŒ…å«äº†æ‰€æœ‰çš„å±žæ€§ï¼Œå¯èƒ½åŒ…å«äº†classNameï¼Œidç­‰ç­‰
  ä»Žç¬¬ä¸‰ä¸ªå‚æ•°å¼€å§‹ï¼Œå°±æ˜¯å®ƒçš„å­èŠ‚ç‚¹

  æˆ‘ä»¬å¯¹createElementçš„å®žçŽ°éžå¸¸ç®€å•ï¼Œåªéœ€è¦è¿”å›žä¸€ä¸ªå¯¹è±¡æ¥ä¿å­˜å®ƒçš„ä¿¡æ¯å°±è¡Œäº†ã€‚
 */
function createElement(tag, attrs, ...children) {
  return {
    tag,
    attrs,
    children
  }
}

/**
  æ‰€ä»¥renderçš„ç¬¬ä¸€ä¸ªå‚æ•°å®žé™…ä¸ŠæŽ¥å—çš„æ˜¯createElementè¿”å›žçš„å¯¹è±¡ï¼Œä¹Ÿå°±æ˜¯è™šæ‹ŸDOM
  è€Œç¬¬äºŒä¸ªå‚æ•°åˆ™æ˜¯æŒ‚è½½çš„ç›®æ ‡DOM

  æ€»è€Œè¨€ä¹‹ï¼Œrenderæ–¹æ³•çš„ä½œç”¨å°±æ˜¯å°†è™šæ‹ŸDOMæ¸²æŸ“æˆçœŸå®žçš„DOMï¼Œä¸‹é¢æ˜¯å®ƒçš„å®žçŽ°ï¼š 
*/
function render ( vnode, container) {
  if (typeof vnode === 'string') {
    const textNode = document.createTextNode(vnode);
    return container.appendChild(textNode)
  }

  const dom = document.createElement(vnode.tag);

  if(vnode.attrs) {
    Object.keys(vnode.attrs).forEach( key => {
      const value = vnode.attrs[key]
      setAttribute(dom, key, value); // è®¾ç½®å±žæ€§
    })
  }

  vnode.children.forEach(child => render(child, dom))

  return container.appendChild(dom)
}

const React = {
  createElement
}
const ReactDOM = {
  render: (vnode, container) => {
    container.innerHTML = ''
    return render(vnode, container)
  }
}

